@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;700;900&display=swap);
:root {
  --side-padding: 40px;
  --content-width: 1100px;
  --dark-blue: #2c4047;
  --bright-green: #00c385;
  --pure-white: #fff;
  --grey-bg: #f7f7f7;
  --accent-orange: #ff9017;
  --bright-red: #fe3210;
  --muted: #969696;
  --font-dark: #212121;
  --font-light: #5b5b5b;
  --border-color: #e4e4e4;
  --star-color-filled: #e5a800;
  --star-color-empty: #cbd4e6;
}

body {
  margin: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f7f7f7;
  background: var(--grey-bg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  box-sizing: border-box;
}

a,
span,
div,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol {
  margin: 0;
  padding: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  color: #212121;
  color: var(--font-dark);
}

a {
  text-decoration: none;
  color: #00c385;
  color: var(--bright-green);
}

@media screen and (max-width: 1100px) {
  :root {
    --content-width: 900px;
  }
}

@media screen and (max-width: 1000px) {
  :root {
    --content-width: 800px;
  }
}

/* Rules for sizing the icon. */
.material-icons.md-18 {
  font-size: 18px;
}
.material-icons.md-24 {
  font-size: 24px;
}
.material-icons.md-36 {
  font-size: 36px;
}
.material-icons.md-48 {
  font-size: 48px;
}

/* Rules for using icons as black on a light background. */
.material-icons.md-dark {
  color: rgba(0, 0, 0, 0.54);
}
.material-icons.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26);
}

/* Rules for using icons as white on a dark background. */
.material-icons.md-light {
  color: rgba(255, 255, 255, 1);
}
.material-icons.md-light.md-inactive {
  color: rgba(255, 255, 255, 0.3);
}

.SubNavbar {
  width: 100%;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  padding: 0 var(--side-padding);
}
.SubNavbar .content {
  max-width: var(--content-width);
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}
.SubNavbar .content .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;
}
.SubNavbar .content .search-bar {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1 1;
  margin-right: 40px;
  /* max-width: 500px; */
  /* width: 100%; */
}
.SubNavbar .content .search-bar input {
  height: 40px;
  padding: 0 15px;
  border-radius: 8px 0px 0px 8px;
  border: solid 1px #e4e4e4;
  flex: 1 1;
  font-size: 16px;
}
.SubNavbar .content .search-bar i {
  height: 40px;
  width: 40px;
  background: var(--bright-green);
  color: white;
  fill: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.SubNavbar .content .links {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  min-width: 400px;
}

.SubNavbar .content .help,
.SubNavbar .content .auth a,
.SubNavbar .content .cart a {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 500;
}

.SubNavbar .content .help i {
  color: var(--muted);
  margin-right: 10px;
}

.SubNavbar .content .auth a {
  color: var(--font-dark);
  margin: 0 5px;
}

.SubNavbar .content .cart {
  color: var(--pure-white);
  background: var(--bright-green);
  width: 170px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
}
.SubNavbar .content .cart i,
.SubNavbar .content .cart a {
  color: var(--pure-white);
  margin: 0 5px;
}
.SubNavbar .hamburger-menu {
  margin-right: 40px;
}
.SubNavbar .hamburger-menu i {
  cursor: pointer;
  color: var(--muted);
}
.SubNavbar .category-menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex: 1 1;
  list-style-type: none;
  height: 40px;
  font-weight: 500;
  transition: all 0.2s ease;
}
.SubNavbar .category-menu.closed {
  opacity: 0;
}
.SubNavbar .category-menu.closed li button {
  -webkit-transform: scale(0.01);
          transform: scale(0.01);
}
.SubNavbar .category-menu li.is-active button {
  border-bottom: solid 2px var(--bright-green);
}
.SubNavbar .category-menu li button {
  cursor: pointer;
  transition: all 0.1s ease;
  border: none;
  background: transparent;
  outline: none;
  font-weight: 500;
  font-size: 18px;
  padding-bottom: 10px;
  border-bottom: solid 2px transparent;
  transition: all 0.2s ease;
}
.SubNavbar .category-menu li button:active {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
}

.Hero {
